//
// --------------------------------------------------------------
// Colours and backgrounds for common.css
// --------------------------------------------------------------

html, body {
	color: $text-color;
	@if $use-wrapper {
		background-color: $page-background;
		&.simple {
			background-color: $content-background;
		}
	}
	@else {
		background-color: $content-background;
	}
}

h1 {
	color: $header-text;
}

h2 {
	color: $page-title-color;
}

h3 {
	color: $stats-title-color;
	a {
		color: inherit;
		&:hover {
			color: $link-hover-color;
		}
	}
}

hr {
	border-color: $content-top-border;
	border-top-color: $content-bottom-border;
}

// Search box
// ---------------------------------------------

.search-box {
	.inputbox {
		border-width: 0;
	}
	.button, .button:after, .button:before {
		border-color: $search-box-button-bg;
		background-color: $search-box-button-bg;
		color: $search-box-button-color;
		&:hover {
			border-color: $search-box-button-bg-hover;
			background-color: $search-box-button-bg-hover;
			color: $search-box-button-color-hover;
		}
	}
}

// Round cornered boxes and backgrounds
// ----------------------------------------
#wrap {
	@if $use-wrapper {
		background-color: $content-background;
		border-color: $content-border-color;
	}
}

.headerbar {
	color: $header-text;
}

#page-header-box {
	background-color: $primary-color;
}

.navbar {
	background-color: $block-background;
	&.secondary {
		color: $link-color;
		> ul > li:hover,
		> ul > li.dropdown-visible {
			&, &:after, &:before {
				color: $secondary-nav-hover-link;
			}
		}
		.dropdown {
			color: $text-color;
		}
	}
	
	&.nav-breadcrumbs {
		//background-color: $primary-color-dark;
		//color: $overlay-color;
		
		// Navigation
		//.breadcrumbs .crumb:before {
		//	color: $muted-color;
		//}
		.breadcrumbs a {
			text-decoration: none;
			color: $primary-color;
			&:hover {
				color: $primary-color-dark;
			}
		}
	}

	.nav-tabs {
		background-color: $primary-nav-bg;
		.tab {
			color: $primary-nav-color;
			&.selected {
				color: $primary-nav-selected-color;
				background-color: $primary-nav-selected-bg;
				border-bottom-color: $primary-nav-selected-bd-color;
			}
			&:hover:not(.selected), &.dropdown-visible:not(.selected) {
				color: $primary-nav-hover-color;
				background-color: $primary-nav-hover-bg;
				border-bottom-color: $primary-nav-hover-bd-color;
			}
			& > strong {
				color: $primary-nav-alert-color;
				background-color: $primary-nav-alert-bg;
				box-shadow: $primary-nav-alert-shadow;
				.arrow {
					border-top-color: $primary-nav-alert-bg;
				}
			}
			.dropdown {
				color: $text-color;
			}
		}
	}
}

div.forum-info {
	background-color: $block-background;
	color: $primary-color;
	
	div.forum-title-group {
		border-left-color: $secondary-color-accent-dark;
		/*h2.forum-title a, p.forum-mods a {
			color: $overlay-color;
			&:hover {
				text-decoration: none;
			}
		}*/
	}
}

div.rules {
	background-color: $primary-color-dark;
	color: $overlay-color;
	& a {
		color: $overlay-color;
		&:hover {
			text-decoration: none;
		}
	}
}

.panel {
	background-color: $block-background;
	color: $text-color;
}

.post {
	h3 {
		color: $block-title-color;
	}
	&:target {
		h3 a {
			color: $bold-text-color;
		}
		.content {
			color: $bold-text-color;
		}
	}
}

.bg1 {
	background-color: $block-background;
}

table.zebra-list tr:nth-child(odd) td, ul.zebra-list li:nth-child(odd) {
	background-color: $block-background;
}

.bg2 {
	background-color: $block-background2;
}

table.zebra-list tr:nth-child(even) td, ul.zebra-list li:nth-child(even) {
	background-color: $block-background2;
}

.bg3 { 
	background-color: $block-background3;
	&.panel {
		background: transparent none;
		background-color: rgba($content-background, .3);
	}
	.section-posting &.panel {
		background-color: $block-background;
	}
}

// Horizontal lists
// ----------------------------------------

ul.navlinks {
	border-top-color: $block-inner-border;
}

// Forums list layout
// ----------------------
.forumtitle {
	.forum_unread &,
	.forum_unread_locked & {
		color: $link-hover-color;
	}
}

.topictitle {
	.global_unread &,
	.global_unread_mine &,
	.global_unread_locked &,
	.global_unread_locked_mine &,
	.announce_unread &,
	.announce_unread_mine &,
	.announce_unread_locked &,
	.announce_unread_locked_mine &,
	.sticky_unread &,
	.sticky_unread_mine &,
	.sticky_unread_locked &,
	.sticky_unread_locked_mine &,
	.topic_unread &,
	.topic_unread_mine &,
	.topic_unread_locked &,
	.topic_unread_locked_mine &,
	.topic_unread_hot &,
	.topic_unread_hot_mine &
	{
		color: $link-hover-color;
	}
}

.forum-statistics {
	color: $muted-color;
}

.forum-lastpost {
	color: $muted-color;
	span:before {
		opacity: .5;
	}
}

.forum-description.toggle {
	color: $forum-description-color;
	background-color: fade-out($forum-description-bg, .2);
	a {
		color: inherit !important;
	}
	span.arrow {
		border-right-color: fade-out($forum-description-bg, .2);
	}
	.rtl & span.arrow {
		border-right-color: transparent;
		border-left-color: fade-out($forum-description-bg, .2);
	}
}

.forum-statistics {
	.value {
		color: $bold-text-color;
	}
}

// Table styles
// ----------------------------------------
table.table1 thead th {
	color: $heading-color;
}

table.table1 tbody tr {
	border-color: $block-inner-border;
}

table.table1 td {
	color: $text-color;
}

table.table1 tbody td {
	border-top-color: $block-inner-border;
}

table.table1 tbody th {
	border-bottom-color: $block-bottom-border;
	color: $bold-text-color;
	background-color: $content-background;
}

table.info tbody th {
	color: $bold-text-color;
}

// Misc layout styles
// ----------------------------------------
dl.details dt {
	color: $bold-text-color;
}

dl.details dd {
	color: $muted-color;
}

.sep {
	color: $primary-color;
}

// Pagination
// ----------------------------------------

.pagination li a {
	color: $link-color;
	background-color: $block-background;
	border-color: $block-background;
}

.pagination li.ellipsis span {
	background-color: transparent;
	color: $bold-text-color;
}

.pagination li.active span {
	color: $overlay-color;
	background-color: $primary-color;
	border-color: $primary-color;
}

.pagination li a:hover, .pagination .dropdown-visible a.dropdown-trigger, .nojs .pagination .dropdown-container:hover a.dropdown-trigger {
	border-color: $link-hover-color;
	background-color: $link-hover-color;
	color: $overlay-color;
}

.pagination li.page-jump a:after {
	color: $link-color;
}

.pagination li.page-jump a:hover:after {
	color: $link-hover-color;
}

// Miscellaneous styles
// ----------------------------------------

.copyright, .copyright a, .copyright a, .copyright a:hover, .copyright a:focus, .copyright a:visited {
	text-decoration: none;
}

.error {
	color: $secondary-color;
}

.reported {
	background-color: $block-background-reported;
	.postprofile, .signature {
		border-color: darken($block-background-reported, 3%);
	}
}

li.reported:hover {
	background-color: $block-background-reported;
}

p.post-notice {
	background-color: mix($block-background, $rules-color, 60%);
	color: $rules-color;
	background-image: none;
}

// Blank Material Design Icons replacement
%post-notice {
	font-family: "Material Design Icons";
}

p.post-notice.deleted:before {
	@extend %post-notice;
	content: mdi("close-octagon");	// icon: mdi-close-octagon
	color: $secondary-color;
}

p.post-notice.unapproved:before {
	@extend %post-notice;
	content: mdi("block-helper");	// icon: mdi-block-helper
	color: $secondary-color;
}

p.post-notice.reported:before, p.post-notice.error:before {
	@extend %post-notice;
	content: mdi("alert-octagon");	// icon: mdi-alert-octagon
	color: $secondary-color;
}

// Topic status
.topic-status.sticky {
	color: $muted-color;
}

//
// --------------------------------------------------------------
// Colours and backgrounds for links.css
// --------------------------------------------------------------

a { color: $link-color; }
a:hover	{ color: $link-hover-color; }

a.time { 
	color: $muted-color; 
	&:hover {
		color: $link-hover-color;
	}
}

// Links on gradient backgrounds
.forumbg .header a, .forabg .header a, th a {
	color: $heading-link;
}

.forumbg .header a:hover, .forabg .header a:hover, th a:hover {
	color: $heading-link-hover;
}

// Notification mark read link
.dropdown-extended a.mark_read {
	background-color: $dropdown-bg;
}

// Back to top of page
a.top, a.top2 {
	color: $muted-color;
	&:hover {
		color: $link-hover-color;
	}
}

// Arrow links

a.feed-icon-forum:after {
	color: mix($muted-color, $block-background, 50%);
}

a.feed-icon-forum:hover:after {
	color: $link-hover-color;
}

//
// --------------------------------------------------------------
// Colours and backgrounds for content.css
// --------------------------------------------------------------


ul.topiclist li {
	color: $text-color;
	@if $split-forums == false {
		.forabg &, .forumbg & {
			background: transparent;
		}
	}
}

li.row {
	border-top-color: $block-top-border;
	border-bottom-color: $block-bottom-border;
	.forum-lastpost strong,
	.subforums-list strong {
		color: $muted-color;
	}
}

li.row strong {
	color: $bold-text-color;
}

// Unread forum/topic
.icon-link + .list-inner > a.forumtitle,
.pm_unread .topictitle,
a.subforum.unread {
	color: $secondary-color;
}

// Post body styles
// ----------------------------------------
.postbody {
	color: $post-text-color;
}

// Content container styles
// ----------------------------------------
.content {
	color: $post-text-color;
}

.content h2, .panel h2 {
	color: $panel-title-color;
	border-bottom-color:  $panel-title-border;
}

dl.faq dt {
	color: $post-text-color;
}

.posthilit {
	background-color: $block-background-reported;
	color: if($light, darken($secondary-color, 10%), lighten($secondary-color, 10%));
}


// Post signature
.signature {
	border-top-color: $block-inner-border;
}

// Post noticies
.notice {
	border-top-color:  $block-inner-border;
}

// BB Code styles
// ----------------------------------------
// Quote block
blockquote {
	background-color: $bbcode-background;
	border-color: $bbcode-outer-border;
	border-left-color: lighten($secondary-color, 15%);
	.rtl & {
		border-left-color: $bbcode-outer-border;
		border-right-color: lighten($secondary-color, 15%);
	}
}

blockquote blockquote,
blockquote .codebox,
blockquote .attachbox {
	// Nested quotes
	background-color: $bbcode-background2;
	background-color: rgba(196, 196, 196, 0.1);
	border-left-color: lighten(desaturate($primary-color, 20%), 20%);
	.rtl & {
		border-left-color: $bbcode-outer-border;
		border-right-color: lighten(desaturate($primary-color, 20%), 20%);
	}
}

blockquote blockquote blockquote,
blockquote blockquote .codebox,
blockquote blockquote .attachbox {
	// Nested quotes
	background-color: $bbcode-background;
	border-left-color: lighten($secondary-color, 15%);
	.rtl & {
		border-left-color: $bbcode-outer-border;
		border-right-color: lighten($secondary-color, 15%);
	}
}

blockquote:before {
	color: $muted-color;
}

// Code block
.codebox {
	color: $faint-color;
	border-color: $bbcode-background-dark;
	border-left-color: $bbcode-code-color;
	background-color: $bbcode-background-dark;
}

.codebox code {
	color: $bbcode-code-color;
}

.syntaxbg		{ color: transparent; }
.syntaxcomment	{ color: saturate(code-color(30deg), 100%); }
.syntaxdefault	{ color: code-color(240deg); }
.syntaxhtml		{ color: $text-color; }
.syntaxkeyword	{ color: code-color(120deg); }
.syntaxstring	{ color: saturate(code-color(0deg), 100%); }

// Attachments
// ----------------------------------------
.attachbox {
	background-color: $bbcode-background;
	border-color:  $bbcode-outer-border;
	border-left-color: lighten(desaturate($primary-color, 20%), 20%);
	.rtl & {
		border-left-color: $bbcode-outer-border;
		border-right-color: lighten(desaturate($primary-color, 20%), 20%);
	}
}

.attachbox dd {
	border-top-color: $bbcode-inner-border;
}

.attachbox p {
	color: $bbcode-heading-text;
}

.attachbox p.stats {
	color: $bbcode-heading-text;
}

// Inline image thumbnails

dl.file dd {
	color: $bbcode-heading-text;
}

dl.thumbnail img {
	border-color: $bbcode-inner-border;
	background-color: $block-background;
}

dl.thumbnail dd {
	color: $bbcode-heading-text;
}

// Post poll styles
// ----------------------------------------

fieldset.polls dl {
	border-top-color: $block-top-border;
	border-bottom-color: $block-bottom-border;
	color: $text-color;
}

fieldset.polls dl.voted {
	color: $bold-text-color;
}

fieldset.polls dd div {
	color: $overlay-color;
}

%pollbar {
	box-shadow: 0 5px 5px -5px rgba(0, 0, 0, 0.2);
}

.pollbar1 {
	@extend %pollbar;
	background-color: desaturate($secondary-color, 30%);
}

.pollbar2 {
	@extend %pollbar;
	background-color: desaturate($secondary-color, 25%);
}

.pollbar3 {
	@extend %pollbar;
	background-color: desaturate($secondary-color, 20%);
}

.pollbar4 {
	@extend %pollbar;
	background-color: desaturate($secondary-color, 10%);
}

.pollbar5 {
	@extend %pollbar;
	background-color: $secondary-color;
}

// Poster profile block
// ----------------------------------------
.postprofile {
	color: $text-color;
	border-color: $block-inner-border;
}

.postprofile strong {
	color: $bold-text-color;
}

.online-ribbon span {
	background-color: $secondary-color;
	color: #fff;
}

dd.profile-warnings {
	color: #BC2A4D;
}

.postbody .content::-webkit-scrollbar, #topicreview::-webkit-scrollbar, #post_details::-webkit-scrollbar, .codebox code::-webkit-scrollbar, .attachbox dd::-webkit-scrollbar, .attach-image::-webkit-scrollbar, .dropdown-extended ul::-webkit-scrollbar {
	background: rgba(0, 0, 0, .1);
}

.postbody .content::-webkit-scrollbar-thumb, #topicreview::-webkit-scrollbar-thumb, #post_details::-webkit-scrollbar-thumb, .codebox code::-webkit-scrollbar-thumb, .attachbox dd::-webkit-scrollbar-thumb, .attach-image::-webkit-scrollbar-thumb, .dropdown-extended ul::-webkit-scrollbar-thumb {
	background: rgba(0, 0, 0, .3);
}

//
// --------------------------------------------------------------
// Colours and backgrounds for buttons.css
// --------------------------------------------------------------
.button {
	&, .buttons > &.locked-icon {
		border-color: $button-border;
		background-color: $button-bg;
		color: $button-text;
	}

	.buttons > &.locked-icon {
		color: $button-icon;
		&:after, &:before {
			color: $button-icon !important;
		}
	}
}

.dropdown-select {
	&:hover, .dropdown-container.dropdown-visible & {
		background-color: $button-bg-hover;
		color: $button-text-hover;
	}
	
}
.dropdown-visible .dropdown-select, .dropdown-visible .dropdown-select:hover, .nojs .dropdown-container:hover .dropdown-select,
.buttons > .button:hover,
.button:hover, .dropdown-visible .dropdown-select, .nojs .dropdown-container:hover .dropdown-select {
	border-color: $button-bg-hover;
	background-color: $button-bg-hover;
	color: $button-text-hover;

	&:after, &:before {
		color: $button-icon-hover !important;
	}
}

.contact-icons a		{ border-color: $block-inner-border; }

// Small icons
%icon-button {
	color: $button-icon;
	&:hover {
		color: $button-icon-hover;
	}
}

%icon-button-visible {
	.dropdown-visible &,
	.nojs .dropdown-container:hover & {
		color: $button-icon-hover;
	}
}

.jabber-icon:after {
	color: #c90000;
}

.phpbb_icq-icon:after {
	color: #60ff00;
	text-shadow: 0 0 1px #000, 0 0 2px #000;
}

.phpbb_wlm-icon:after {
	color: #75afd3;
}

.phpbb_aol-icon:after {
	color: #ffd800;
	text-shadow: 0 0 1px #000, 0 0 2px #000;
}

// Icon images
// ----------------------------------------
%icon-image {
	&:after {
		color: $link-color;
	}
	&:hover:after {
		color: $link-hover-color;
	}
}

%icon-search-new {
	&:after {
		color: $primary-color;
	}
}

%icon-search-misc {
	&:after {
		color: $primary-color;
	}
}

.icon-home {
	&.breadcrumbs:after {
		color: $primary-color;
	}
}

.icon-notification, .icon-pm {
	&.non-zero:after {
		color: $secondary-color;
	}
}

.icon-time {
	&:after {
		color: $faint-color;
	}
}

// Forum icons & Topic icons
.global_read													{ background-image: url("./images/announce_read.png"); }
.global_read_mine												{ @extend .global_read; }
.global_read_locked												{ background-image: url("./images/announce_read_locked.png"); }
.global_read_locked_mine										{ @extend .global_read_locked; }
.global_unread													{ background-image: url("./images/announce_unread.png"); }
.global_unread_mine												{ @extend .global_unread; }
.global_unread_locked											{ background-image: url("./images/announce_unread_locked.png"); }
.global_unread_locked_mine										{ @extend .global_unread_locked; }

.announce_read													{ @extend .global_read; }
.announce_read_mine												{ @extend .global_read_mine; }
.announce_read_locked											{ @extend .global_read_locked; }
.announce_read_locked_mine										{ @extend .global_read_locked_mine; }
.announce_unread												{ @extend .global_unread; }
.announce_unread_mine											{ @extend .global_unread_mine; }
.announce_unread_locked											{ @extend .global_unread_locked; }
.announce_unread_locked_mine									{ @extend .global_unread_locked_mine; }

.forum_link														{ background-image: url("./images/forum_link.png"); }
.forum_read														{ background-image: url("./images/forum_read.png"); }
.forum_read_locked												{ background-image: url("./images/forum_read_locked.png"); }
.forum_read_subforum											{ background-image: url("./images/forum_read_subforum.png"); }
.forum_unread													{ background-image: url("./images/forum_unread.png"); }
.forum_unread_locked											{ background-image: url("./images/forum_unread_locked.png"); }
.forum_unread_subforum											{ background-image: url("./images/forum_unread_subforum.png"); }

.sticky_read													{ @extend .topic_read; }
.sticky_read_mine												{ @extend .topic_read_mine; }
.sticky_read_locked												{ @extend .topic_read_locked; }
.sticky_read_locked_mine										{ @extend .topic_read_locked_mine; }
.sticky_unread													{ @extend .topic_unread; }
.sticky_unread_mine												{ @extend .topic_unread_mine; }
.sticky_unread_locked											{ @extend .topic_unread_locked; }
.sticky_unread_locked_mine										{ @extend .topic_unread_locked_mine; }

.topic_moved													{ background-image: url("./images/topic_moved.png"); }
.topic_read														{ background-image: url("./images/topic_read.png"); }
.topic_read_mine												{ background-image: url("./images/topic_read_mine.png"); }
.topic_read_hot													{ background-image: url("./images/topic_read_hot.png"); }
.topic_read_hot_mine											{ background-image: url("./images/topic_read_hot_mine.png"); }
.topic_read_locked												{ background-image: url("./images/topic_read_locked.png"); }
.topic_read_locked_mine											{ background-image: url("./images/topic_read_locked_mine.png"); }
.topic_unread,													{ background-image: url("./images/topic_unread.png"); }
.topic_unread_mine												{ background-image: url("./images/topic_unread_mine.png"); }
.topic_unread_hot												{ background-image: url("./images/topic_unread_hot.png"); }
.topic_unread_hot_mine											{ background-image: url("./images/topic_unread_hot_mine.png"); }
.topic_unread_locked											{ background-image: url("./images/topic_unread_locked.png"); }
.topic_unread_locked_mine										{ background-image: url("./images/topic_unread_locked_mine.png"); }

.pm_read														{ @extend .topic_read; }
.pm_unread														{ @extend .topic_unread; }

@media (-webkit-min-device-pixel-ratio: 2), (min-resolution: 192dpi), (min-resolution: 1.5dppx)
{
	.global_read, .global_read_locked, .global_unread, .global_unread_locked,
	.forum_link, .forum_read, .forum_read_locked, .forum_read_subforum, .forum_unread, .forum_unread_locked, .forum_unread_subforum,
	.topic_moved, .topic_read, .topic_read_mine, .topic_read_hot, .topic_read_hot_mine, .topic_read_locked, .topic_read_locked_mine,
	.topic_unread, .topic_unread_mine, .topic_unread_hot, .topic_unread_hot_mine, .topic_unread_locked, .topic_unread_locked_mine
	{
		background-size: 36px 36px;
	}

	.global_read												{ background-image: url("./images/announce_read_hd.png"); }
	.global_read_locked											{ background-image: url("./images/announce_read_locked_hd.png"); }
